// Menu

.collapsible-menu {

  button {
    outline: none;
  }

  .collapsible-menu-toggle {
    display: none;

    @include breakpoint(map-get($breakpoints, small)) {
      display: block;
    }
  }

  .collapsible-menu-items {
    li {
      display: inline-block;
      margin-right: 25px;
      font-size: $base-font-size * 0.9;

      a {
        color: $color-text;

        &:hover {
          color: $action-color;
        }
      }

      &.active a {
        color: $action-color;
      }
    }

    @include breakpoint(map-get($breakpoints, small)) {
      display: none;

      margin-left: 0;
      padding-left: 0;

      li {
        display: block;
      }
    }
  }
}

// Cards

.card {
  background-color: white;
  border: $base-border;
  padding: 18px;

  .title
  {
    font-weight: bold;
    font-size: $font-size-large;
    margin-bottom: 10px;
  }
}

// Forms

.search {
  @include clearfix;
  width: 100%;

  input {
    float: left;
    margin: 0;
  }

  input[type="text"] {
    max-width: 60%;
    margin-right: 8px;
  }
}

.login {
  input {
    width: 20em;
  }

  label, button {
    margin-top: 15px;
  }
}

// Accepted status

.accepted-status {
  font-size: $font-size-small;
  display: inline-block;

  .accepted-label {
    background-color: $color-green;
    padding: 2px 22px 2px 10px;    
  }

  select {
    font-size: $font-size-small;

    &.pending {
      background-color: white;
      border: $base-border;    
    }

    &.rejected {
      background-color: white;
      color: $color-red-light;
      border: $base-border;
    }
  }
}

// Filters

.filter-group {
  .dropdown {
    display: inline-block;
    width: 200px;
  }
}

